<template><undefined>

<h1>Modal 对话框</h1>
<p>模态对话框。</p>
<h2 id="he-shi-shi-yong"><a class="header-anchor" href="#he-shi-shi-yong" aria-hidden="true">¶</a> 何时使用</h2>
<p>需要用户处理事务，又不希望跳转页面以致打断工作流程时，可以使用 <code v-pre="">Modal</code> 在当前页面正中打开一个浮层，承载相应的操作。</p>
<p>另外当需要一个简洁的确认框询问用户时，可以使用精心封装好的 <code v-pre="">Vue.$modal.confirm()</code> 等方法。</p>
<h2 id="dai-ma-yan-shi"><a class="header-anchor" href="#dai-ma-yan-shi" aria-hidden="true">¶</a> 代码演示</h2>
<demo-box :jsfiddle="{&quot;html&quot;:&quot;&lt;template&gt;\n    &lt;v-button type=\&quot;primary\&quot; @click=\&quot;showModal\&quot;&gt;显示对话框&lt;/v-button&gt;\n    &lt;v-modal title=\&quot;第一个 Modal\&quot; :visible=\&quot;visible\&quot; @ok=\&quot;handleOk\&quot; @cancel=\&quot;handleCancel\&quot;&gt;\n        &lt;p&gt;对话框的内容&lt;/p&gt;\n        &lt;p&gt;对话框的内容&lt;/p&gt;\n        &lt;p&gt;对话框的内容&lt;/p&gt;\n    &lt;/v-modal&gt;\n&lt;/template&gt;\n\n\n&quot;,&quot;script&quot;:&quot;\n    export default {\n        data () {\n            return {\n                visible: false\n            }\n        },\n\n        methods: {\n            showModal () {\n                this.visible = true;\n            },\n\n            handleOk () {\n                this.visible = false;\n            },\n\n            handleCancel () {\n                this.visible = false;\n            }\n        }\n    }\n&quot;,&quot;style&quot;:null}">
                  <div class="box-demo-show" slot="component"><template>
    <v-button type="primary" @click="showModal">显示对话框</v-button>
    <v-modal title="第一个 Modal" :visible="visible" @ok="handleOk" @cancel="handleCancel">
        <p>对话框的内容</p>
        <p>对话框的内容</p>
        <p>对话框的内容</p>
    </v-modal>
</template>


</div>
                  <div slot="description"><h4 id="ji-ben"><a class="header-anchor" href="#ji-ben" aria-hidden="true">¶</a> 基本</h4>
<p>第一个对话框。</p>
</div>
                  <div class="highlight" slot="code"><pre><code class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">v-button</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"primary"</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"showModal"</span>&gt;</span>显示对话框<span class="hljs-tag">&lt;/<span class="hljs-name">v-button</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">v-modal</span> <span class="hljs-attr">title</span>=<span class="hljs-string">"第一个 Modal"</span>
             <span class="hljs-attr">:visible</span>=<span class="hljs-string">"visible"</span>
             <span class="hljs-attr">@ok</span>=<span class="hljs-string">"handleOk"</span>
             <span class="hljs-attr">@cancel</span>=<span class="hljs-string">"handleCancel"</span>&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">p</span>&gt;</span>对话框的内容<span class="hljs-tag">&lt;/<span class="hljs-name">p</span>&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">p</span>&gt;</span>对话框的内容<span class="hljs-tag">&lt;/<span class="hljs-name">p</span>&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">p</span>&gt;</span>对话框的内容<span class="hljs-tag">&lt;/<span class="hljs-name">p</span>&gt;</span>
    <span class="hljs-tag">&lt;/<span class="hljs-name">v-modal</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">script</span> &gt;</span><span class="javascript">
    <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
        data () {
            <span class="hljs-keyword">return</span> {
                <span class="hljs-attr">visible</span>: <span class="hljs-literal">false</span>
            }
        },

        <span class="hljs-attr">methods</span>: {
            showModal () {
                <span class="hljs-keyword">this</span>.visible = <span class="hljs-literal">true</span>;
            },

            handleOk () {
                <span class="hljs-keyword">this</span>.visible = <span class="hljs-literal">false</span>;
            },

            handleCancel () {
                <span class="hljs-keyword">this</span>.visible = <span class="hljs-literal">false</span>;
            }
        }
    }
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
</div>
