<demo-box :jsfiddle="{&quot;html&quot;:&quot;&lt;template&gt;\n    &lt;v-button type=\&quot;primary\&quot; @click=\&quot;showAsyncModal\&quot;&gt;显示对话框&lt;/v-button&gt;\n    &lt;v-modal title=\&quot;异步关闭的 Modal 对话框\&quot; :visible=\&quot;asyncModalVisible\&quot; @ok=\&quot;handleAsyncOk\&quot; @cancel=\&quot;handleAsyncCancel\&quot; :confirm-loading=\&quot;asyncConfirmLoading\&quot;&gt;\n        &lt;p&gt;对话框的内容&lt;/p&gt;\n        &lt;p&gt;对话框的内容&lt;/p&gt;\n        &lt;p&gt;对话框的内容&lt;/p&gt;\n    &lt;/v-modal&gt;\n&lt;/template&gt;\n\n\n&quot;,&quot;script&quot;:&quot;\n    export default {\n        data () {\n            return {\n                asyncModalVisible: false,\n                asyncConfirmLoading: false\n            }\n        },\n\n        methods: {\n            showAsyncModal () {\n                this.asyncModalVisible = true;\n            },\n\n            handleAsyncOk () {\n                /* 对话框将在两秒后关闭 */\n                this.asyncConfirmLoading = true;\n                setTimeout(() =&gt; {\n                    this.asyncModalVisible = false;\n                    this.asyncConfirmLoading = false;\n                }, 2000);\n            },\n\n            handleAsyncCancel () {\n                this.asyncModalVisible = false;\n            }\n        }\n    }\n&quot;,&quot;style&quot;:null}">
                  <div class="box-demo-show" slot="component"><template>
    <v-button type="primary" @click="showAsyncModal">显示对话框</v-button>
    <v-modal title="异步关闭的 Modal 对话框" :visible="asyncModalVisible" @ok="handleAsyncOk" @cancel="handleAsyncCancel" :confirm-loading="asyncConfirmLoading">
        <p>对话框的内容</p>
        <p>对话框的内容</p>
        <p>对话框的内容</p>
    </v-modal>
</template>


</div>
                  <div slot="description"><h4 id="yi-bu-guan-bi"><a class="header-anchor" href="#yi-bu-guan-bi" aria-hidden="true">¶</a> 异步关闭</h4>
<p>点击确定后异步关闭对话框，例如提交表单。</p>
</div>
                  <div class="highlight" slot="code"><pre><code class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">v-button</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"primary"</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"showAsyncModal"</span>&gt;</span>显示对话框<span class="hljs-tag">&lt;/<span class="hljs-name">v-button</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">v-modal</span> <span class="hljs-attr">title</span>=<span class="hljs-string">"异步关闭的 Modal 对话框"</span>
             <span class="hljs-attr">:visible</span>=<span class="hljs-string">"asyncModalVisible"</span>
             <span class="hljs-attr">@ok</span>=<span class="hljs-string">"handleAsyncOk"</span>
             <span class="hljs-attr">@cancel</span>=<span class="hljs-string">"handleAsyncCancel"</span>
             <span class="hljs-attr">:confirm-loading</span>=<span class="hljs-string">"asyncConfirmLoading"</span>&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">p</span>&gt;</span>对话框的内容<span class="hljs-tag">&lt;/<span class="hljs-name">p</span>&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">p</span>&gt;</span>对话框的内容<span class="hljs-tag">&lt;/<span class="hljs-name">p</span>&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">p</span>&gt;</span>对话框的内容<span class="hljs-tag">&lt;/<span class="hljs-name">p</span>&gt;</span>
    <span class="hljs-tag">&lt;/<span class="hljs-name">v-modal</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
    <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
        data () {
            <span class="hljs-keyword">return</span> {
                <span class="hljs-attr">asyncModalVisible</span>: <span class="hljs-literal">false</span>,
                <span class="hljs-attr">asyncConfirmLoading</span>: <span class="hljs-literal">false</span>
            }
        },

        <span class="hljs-attr">methods</span>: {
            showAsyncModal () {
                <span class="hljs-keyword">this</span>.asyncModalVisible = <span class="hljs-literal">true</span>;
            },

            handleAsyncOk () {
                <span class="hljs-comment">/* 对话框将在两秒后关闭 */</span>
                <span class="hljs-keyword">this</span>.asyncConfirmLoading = <span class="hljs-literal">true</span>;
                setTimeout(<span class="hljs-function"><span class="hljs-params">()</span> =&gt;</span> {
                    <span class="hljs-keyword">this</span>.asyncModalVisible = <span class="hljs-literal">false</span>;
                    <span class="hljs-keyword">this</span>.asyncConfirmLoading = <span class="hljs-literal">false</span>;
                }, <span class="hljs-number">2000</span>);
            },

            handleAsyncCancel () {
                <span class="hljs-keyword">this</span>.asyncModalVisible = <span class="hljs-literal">false</span>;
            }
        }
    }
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
</div>
