<demo-box :jsfiddle="{&quot;html&quot;:&quot;&lt;template&gt;\n    &lt;v-button type=\&quot;primary\&quot; @click=\&quot;showCustomFooter\&quot;&gt;显示对话框&lt;/v-button&gt;\n    &lt;v-modal title=\&quot;自定义页脚的 Modal 对话框\&quot; :visible=\&quot;customFooterVisible\&quot; @cancel=\&quot;customFooterCancel\&quot;&gt;\n        &lt;p&gt;对话框的内容&lt;/p&gt;\n        &lt;p&gt;对话框的内容&lt;/p&gt;\n        &lt;p&gt;对话框的内容&lt;/p&gt;\n        &lt;div slot=\&quot;footer\&quot;&gt;\n            &lt;v-button key=\&quot;cancel\&quot; type=\&quot;ghost\&quot; size=\&quot;large\&quot; @click=\&quot;customFooterCancel\&quot;&gt;\n                返 回\n            &lt;/v-button&gt;\n            &lt;v-button key=\&quot;confirm\&quot; type=\&quot;primary\&quot; size=\&quot;large\&quot; :loading=\&quot;customFooterLoading\&quot; @click=\&quot;customFooterOk\&quot;&gt;\n                提 交\n            &lt;/v-button&gt;\n        &lt;/div&gt;\n    &lt;/v-modal&gt;\n&lt;/template&gt;\n\n\n&quot;,&quot;script&quot;:&quot;\n    export default {\n        data () {\n            return {\n                customFooterVisible: false,\n                customFooterLoading: false\n            }\n        },\n\n        methods: {\n            customFooterOk () {\n                /* 对话框将在两秒后关闭 */\n                this.customFooterLoading = true;\n                setTimeout(() =&gt; {\n                    this.customFooterVisible = false;\n                    this.customFooterLoading = false;\n                }, 2000);\n            },\n\n            customFooterCancel () {\n                this.customFooterVisible = false;\n            }\n        }\n    }\n&quot;,&quot;style&quot;:null}">
                  <div class="box-demo-show" slot="component"><template>
    <v-button type="primary" @click="showCustomFooter">显示对话框</v-button>
    <v-modal title="自定义页脚的 Modal 对话框" :visible="customFooterVisible" @cancel="customFooterCancel">
        <p>对话框的内容</p>
        <p>对话框的内容</p>
        <p>对话框的内容</p>
        <div slot="footer">
            <v-button key="cancel" type="ghost" size="large" @click="customFooterCancel">
                返 回
            </v-button>
            <v-button key="confirm" type="primary" size="large" :loading="customFooterLoading" @click="customFooterOk">
                提 交
            </v-button>
        </div>
    </v-modal>
</template>


</div>
