    <span class="hljs-tag">&lt;<span class="hljs-name">v-button</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"warning"</span>&gt;</span>警告提示<span class="hljs-tag">&lt;/<span class="hljs-name">v-button</span>&gt;</span>
