<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
    <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
        data () {
            <span class="hljs-keyword">return</span> {
                <span class="hljs-attr">modalStyle</span>: {
                    <span class="hljs-attr">top</span>: <span class="hljs-string">'20px'</span>
                },
                <span class="hljs-attr">customStyleVisible</span>: <span class="hljs-literal">false</span>,
                <span class="hljs-attr">customStyleVisible1</span>: <span class="hljs-literal">false</span>
            }
        },

        <span class="hljs-attr">methods</span>: {
            showCustomStyleModal () {
                <span class="hljs-keyword">this</span>.customStyleVisible = <span class="hljs-literal">true</span>;
            },

            customStyleOK () {
                <span class="hljs-keyword">this</span>.customStyleVisible = <span class="hljs-literal">false</span>;
            },

            customStyleCancel () {
                <span class="hljs-keyword">this</span>.customStyleVisible = <span class="hljs-literal">false</span>;
            },

            showCustomStyleModal1 () {
                <span class="hljs-keyword">this</span>.customStyleVisible1 = <span class="hljs-literal">true</span>;
            },

            customStyleOK1 () {
                <span class="hljs-keyword">this</span>.customStyleVisible1 = <span class="hljs-literal">false</span>;
            },

            customStyleCancel1 () {
                <span class="hljs-keyword">this</span>.customStyleVisible1 = <span class="hljs-literal">false</span>;
            },

            showCustomStyleConfirm () {
                <span class="hljs-keyword">this</span>.$modal.confirm({
                    <span class="hljs-attr">iconType</span>: <span class="hljs-string">'frown'</span>,
                    <span class="hljs-attr">title</span>: <span class="hljs-string">'确认删除'</span>,
                    <span class="hljs-attr">content</span>: <span class="hljs-string">'一些解释内容'</span>,
                    <span class="hljs-attr">okText</span>: <span class="hljs-string">'是的'</span>,
                    <span class="hljs-attr">cancelText</span>: <span class="hljs-string">'我再想想'</span>,
                    <span class="hljs-attr">width</span>: <span class="hljs-number">300</span>
                })
            }
        }
    }
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">style</span>&gt;</span><span class="css">
    <span class="hljs-selector-class">.vertical-center-modal</span> {
        <span class="hljs-attribute">display</span>: flex;
        <span class="hljs-attribute">align-items</span>: center;
        <span class="hljs-attribute">justify-content</span>: center;
    }
</span><span class="hljs-tag">&lt;/<span class="hljs-name">style</span>&gt;</span>
</code></pre>
</demo-box>
<demo-box :jsfiddle="{&quot;html&quot;:&quot;&lt;template&gt;\n    &lt;v-button @click=\&quot;_confirmDestroy\&quot;&gt;显示成功提示&lt;/v-button&gt;\n&lt;/template&gt;\n\n\n&quot;,&quot;script&quot;:&quot;\n    export default {\n        data () {\n            return {}\n        },\n\n        methods: {\n            _confirmDestroy() {\n                const modal = this.$modal.success({\n                    title: '这是一条通知信息',\n                    content: '一秒后自动移除'\n                });\n                setTimeout(() =&gt; modal.destroy(), 1000);\n            }\n        }\n    }\n&quot;,&quot;style&quot;:null}">
