    <v-button @click="_confirmDestroy">显示成功提示</v-button>
</template>


</div>
                  <div slot="description"><h4 id="shou-dong-yi-chu"><a class="header-anchor" href="#shou-dong-yi-chu" aria-hidden="true">¶</a> 手动移除</h4>
<p>手动关闭modal。</p>
</div>
                  <div class="highlight" slot="code"><pre><code class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">v-button</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"_confirmDestroy"</span>&gt;</span>显示成功提示<span class="hljs-tag">&lt;/<span class="hljs-name">v-button</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
    <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
        data () {
            <span class="hljs-keyword">return</span> {}
        },

        <span class="hljs-attr">methods</span>: {
            _confirmDestroy() {
                <span class="hljs-keyword">const</span> modal = <span class="hljs-keyword">this</span>.$modal.success({
                    <span class="hljs-attr">title</span>: <span class="hljs-string">'这是一条通知信息'</span>,
                    <span class="hljs-attr">content</span>: <span class="hljs-string">'一秒后自动移除'</span>
                });
                setTimeout(<span class="hljs-function"><span class="hljs-params">()</span> =&gt;</span> modal.destroy(), <span class="hljs-number">1000</span>);
            }
        }
    }
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
</div>
                <summary>
  #### 手动移除
  手动关闭modal。
</summary>
<pre v-pre=""><code v-pre="" class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">v-button</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"_confirmDestroy"</span>&gt;</span>显示成功提示<span class="hljs-tag">&lt;/<span class="hljs-name">v-button</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
    <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
        data () {
            <span class="hljs-keyword">return</span> {}
        },

        <span class="hljs-attr">methods</span>: {
            _confirmDestroy() {
                <span class="hljs-keyword">const</span> modal = <span class="hljs-keyword">this</span>.$modal.success({
                    <span class="hljs-attr">title</span>: <span class="hljs-string">'这是一条通知信息'</span>,
                    <span class="hljs-attr">content</span>: <span class="hljs-string">'一秒后自动移除'</span>
                });
                setTimeout(<span class="hljs-function"><span class="hljs-params">()</span> =&gt;</span> modal.destroy(), <span class="hljs-number">1000</span>);
            }
        }
    }
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
