<h2 id="api"><a class="header-anchor" href="#api" aria-hidden="true">¶</a> API</h2>
<h3 id="modal-props"><a class="header-anchor" href="#modal-props" aria-hidden="true">¶</a> Modal Props</h3>
<table class="table"><thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>visible</td>
<td>对话框是否可见</td>
<td>boolean</td>
<td>false</td>
</tr>
<tr>
<td>title</td>
<td>标题</td>
<td>string</td>
<td>-</td>
</tr>
<tr>
<td>closable</td>
<td>是否显示右上角的关闭按钮</td>
<td>boolean</td>
<td>true</td>
</tr>
<tr>
<td>hasMask</td>
<td>是否显示蒙层</td>
<td>boolean</td>
<td>true</td>
</tr>
<tr>
<td>maskClosable</td>
<td>点击蒙层是否允许关闭</td>
<td>boolean</td>
<td>true</td>
</tr>
<tr>
<td>confirmLoading</td>
<td>确定按钮 loading</td>
<td>boolean</td>
<td>false</td>
</tr>
<tr>
<td>okText</td>
<td>确认按钮文字</td>
<td>string</td>
<td><code v-pre="">确定</code></td>
</tr>
<tr>
<td>cancelText</td>
<td>取消按钮文字</td>
<td>string</td>
<td><code v-pre="">取消</code></td>
</tr>
<tr>
<td>slot:footer</td>
<td>底部内容</td>
<td>string</td>
<td>确定取消按钮</td>
</tr>
<tr>
<td>wrapClassName</td>
<td>对话框外层容器的类名</td>
<td>string</td>
<td>-</td>
</tr>
<tr>
<td>width</td>
<td>宽度</td>
<td>number</td>
<td>520</td>
</tr>
<tr>
<td>modalStyle</td>
<td>可用于设置浮层的样式，调整浮层位置等</td>
<td>object</td>
<td>-</td>
</tr>
</tbody>
</table>
<h3 id="modal-events"><a class="header-anchor" href="#modal-events" aria-hidden="true">¶</a> Modal Events</h3>
<table class="table"><thead>
<tr>
<th>事件名称</th>
<th>说明</th>
<th>回调参数</th>
</tr>
</thead>
<tbody>
<tr>
<td>ok</td>
<td>点击确定时触发</td>
<td>-</td>
</tr>
<tr>
<td>cancel</td>
<td>点击遮罩层或右上角叉或取消按钮时触发</td>
<td>-</td>
</tr>
</tbody>
</table>
<h3 id="vue-modal-xxx"><a class="header-anchor" href="#vue-modal-xxx" aria-hidden="true">¶</a> Vue.$modal.xxx()</h3>
<p>包括：</p>
<ul>
<li><code v-pre="">Modal.info</code></li>
<li><code v-pre="">Modal.success</code></li>
<li><code v-pre="">Modal.error</code></li>
<li><code v-pre="">Modal.warning</code></li>
<li><code v-pre="">Modal.confirm</code></li>
</ul>
<p>以上均为一个函数，参数为 object，具体属性如下：</p>
<table class="table"><thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>iconType</td>
<td>图标 Icon 类型</td>
<td>string</td>
<td>question-circle</td>
</tr>
<tr>
<td>title</td>
<td>标题</td>
<td>string</td>
<td>-</td>
</tr>
<tr>
<td>content</td>
<td>内容</td>
<td>string</td>
<td>-</td>
</tr>
<tr>
<td>width</td>
<td>宽度</td>
<td>number</td>
<td>416</td>
</tr>
<tr>
<td>okText</td>
<td>确认按钮文字</td>
<td>string</td>
<td>确定(只有确定按钮时为<code v-pre="">知道了</code>)</td>
</tr>
<tr>
<td>cancelText</td>
<td>取消按钮文字</td>
<td>string</td>
<td>取消</td>
</tr>
<tr>
<td>onOk</td>
<td>点击确定回调，参数为关闭函数，返回 promise 时 resolve 后自动关闭</td>
<td>function</td>
<td>-</td>
</tr>
<tr>
<td>onCancel</td>
<td>取消回调，参数为关闭函数，返回 promise 时 resolve 后自动关闭</td>
<td>function</td>
<td>-</td>
</tr>
<tr>
<td>maskClosable</td>
<td>点击蒙层是否允许关闭</td>
<td>boolean</td>
<td>false</td>
</tr>
<tr>
<td>modalStyle</td>
<td>可用于设置浮层的样式，调整浮层位置等</td>
<td>object</td>
<td>-</td>
</tr>
</tbody>
</table>
</undefined></template>
<style>
    .vertical-center-modal {
        display: flex;
        align-items: center;
        justify-content: center;
    }
</style>
<script>
    export default {
        data () {
            return {
                confirmLoading: false,
                visible: false,
                customFooterVisible: false,
                visible3: false,
                customTextVisible: false,
                customStyleVisible: false,
                customStyleVisible1: false,
                asyncModalVisible: false,
                customFooterLoading: false,
                asyncConfirmLoading: false,
                modalStyle: {
                    top: '20px',
                },
                wrapClassName: 'vertical-center-modal'
            }
        },    
        methods: {
            showModal () {
                this.visible = true;
            },

            handleOk () {
                this.confirmLoading = false;
                this.visible = false;
            },

            handleCancel () {
                this.visible = false;
            },

            showCustomFooter () {
                this.customFooterVisible = true;
            },

            customFooterOk () {
                /* 对话框将在两秒后关闭 */
                this.customFooterLoading = true;
                setTimeout(() => {
                    this.customFooterVisible = false;
                    this.customFooterLoading = false;
                }, 2000);
            },

            customFooterCancel () {
                this.customFooterVisible = false;
            },

            showCustomTextModal () {
                this.customTextVisible = true;
            },

            customTextOk () {
                this.customTextVisible = false;
            },

            customTextCancel () {
                this.customTextVisible = false;
            },

            showCustomStyleModal () {
                this.customStyleVisible = true;
            },

            customStyleOK () {
                this.customStyleVisible = false;
            },

            customStyleCancel () {
                this.customStyleVisible = false;
            },

            showCustomStyleModal1 () {
                this.customStyleVisible1 = true;
            },

            customStyleOK1 () {
                this.customStyleVisible1 = false;
            },

            customStyleCancel1 () {
                this.customStyleVisible1 = false;
            },

            /* Async Modal */
            showAsyncModal () {
                this.asyncModalVisible = true;
            },

            handleAsyncOk () {
                /* 对话框将在两秒后关闭 */
                this.asyncConfirmLoading = true;
                setTimeout(() => {
                    this.asyncModalVisible = false;
                    this.asyncConfirmLoading = false;
                }, 2000);
            },

            handleAsyncCancel () {
                this.asyncModalVisible = false;
            },

            showCustomTextConfirm() {
                this.$modal.confirm({
                    title: 'Confirm',
                    content: 'Bla bla ...',
                    okText: 'OK',
                    cancelText: 'Cancel'
                })
            },

            showConfirm () {
                this.$modal.confirm({
                    title: '您是否确认要删除这项内容',
                    content: '一些解释内容',
                    onOk: function () {
                        console.log('确定')
                    },
                    onCancel: function () {
                    }
                })
            },

            showAsyncConfirm () {
                this.$modal.confirm({
                    title: '您是否确认要删除这项内容',
                    content: '点确认 1 秒后关闭',
                    onOk: async () => {
                        await new Promise(function (resolve) {
                            setTimeout(resolve, 1000);
                        })
                    },
                    onCancel: function () {
                        return new Promise(function (resolve) {
                            setTimeout(resolve, 1000);
                        })
                    }
                })
            },
             
            showCustomStyleConfirm () {
                this.$modal.confirm({
                    iconType: 'frown',
                    title: '确认删除',
                    content: '一些解释内容',
                    okText: '是的',
                    cancelText: '我再想想',
                    width: 300,
                })
            },

            info() {
                this.$modal.info({
                    maskClosable: true,
                    title: '这是一条通知信息',
                    content: '一些附加信息'
                })
            },

            success() {
                this.$modal.success({
                    maskClosable: true,
                    title: '这是一条通知信息',
                    content: '一些附加信息'
                })
            },

            error() {
                this.$modal.error({
                    title: '这是一条通知信息',
                    content: '一些附加信息'
                })
            },

             warning() {
                 this.$modal.warning({
                     title: '这是一条警告信息',
                     content: '一些附加信息'
                 })
             },

            _confirmDestroy() {
                const modal = this.$modal.success({
                    title: '这是一条通知信息',
                    content: '一秒后自动移除'
                });
                setTimeout(() => modal.destroy(), 1000);
            }
        }
    }
</script>


